<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <!-- 警告提示 -->
      <el-alert title="注意：只允许为第三级分类设置参数" type="warning" show-icon :closable="false">
      </el-alert>
      <!-- 级联选择器 -->
      <el-form class="selectClassify">
        <el-form-item label=" 选择商品分类:">
          <el-cascader v-model="selectKeys" :options="goodsClassifyData" :props="cascaderProps" @change="cascaderChange"></el-cascader>
        </el-form-item>
      </el-form>
      <!-- 标签页 -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="动态参数" name="many">
          <el-button type="primary" :disabled="isBtnDisabled" @click="addParamsVisible = true">添加参数</el-button>
          <!-- 动态参数表格 -->
          <el-table :data="manyData" style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-row>
                  <el-col :span="24">
                    <div class="grid-content bg-purple">
                      <!-- 标签 -->
                      <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="deleteTag(scope.row,i)">
                        {{item}}
                      </el-tag>
                      <!-- 添加标签 -->
                      <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" v-focus size="small" @keyup.enter.native="saveTags(scope.row)"
                        @blur="saveTags(scope.row)">
                      </el-input>
                      <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                    </div>
                  </el-col>
                </el-row>
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="attr_name" label="参数名称">
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click="showEditParams(scope.row)">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" @click=" deleteParams(scope.row.attr_id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only">
          <el-button type="primary" :disabled="isBtnDisabled" @click="addParamsVisible = true">添加属性</el-button>
          <!-- 静态属性表格 -->
          <el-table :data="onlyData" style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-row>
                  <el-col :span="24">
                    <div class="grid-content bg-purple">
                      <!-- 标签 -->
                      <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="deleteTag(scope.row,i)">
                        {{item}}
                      </el-tag>
                      <!-- 添加标签 -->
                      <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" v-focus size="small" @keyup.enter.native="saveTags(scope.row)"
                        @blur="saveTags(scope.row)">
                      </el-input>
                      <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                    </div>
                  </el-col>
                </el-row>
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="attr_name" label="参数名称">
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click="showEditParams(scope.row)"> 编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" @click="deleteParams(scope.row.attr_id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- dialog--添加参数表格 -->
    <el-dialog :title="'添加'+paramsTitle" :visible.sync="addParamsVisible" @close="addParamsClose">
      <el-form :model="addParamsForm" :rules="addParamsRules" ref="addParamsRef" label-width="100px">
        <el-form-item :label="paramsTitle" prop="attr_name">
          <el-input v-model="addParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addParamsVisible = false">取 消</el-button>
        <el-button type="primary" @keyup.enter.native="addParamsSure" @click="addParamsSure">确 定</el-button>
      </span>
    </el-dialog>
    <!-- dialog--编辑参数表格 -->
    <el-dialog :title="'编辑'+paramsTitle" :visible.sync="editParamsVisible" @close="editParamsClose">
      <el-form :model="editParamsForm" :rules="editParamsRules" ref="editParamsRef" label-width="100px">
        <el-form-item :label="paramsTitle" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editParamsVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParamsSure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getGoodsClassifyData()
  },
  data() {
    return {
      // 商品分类信息数据
      goodsClassifyData: [],

      // 级联选择器选中的数据,必须是数组
      selectKeys: [],
      cascaderProps: {
        value: 'cat_id', // 指定选项的值为选项对象的某个属性值
        label: 'cat_name', // 指定选项标签为选项对象的某个属性值
        children: 'children', // 	指定选项的子选项为选项对象的某个属性值
        expandTrigger: 'hover', // 次级菜单的展开方式
      },

      // 默认选中的标签页
      activeName: 'many',
      // 动态属性数据
      manyData: [],
      // 静态属性数据
      onlyData: [],

      // 添加参数dialog显示与隐藏
      addParamsVisible: false,
      // 添加参数的dialog表单数据
      addParamsForm: {
        attr_name: '',
      },
      // 添加参数的dialog表单验证
      addParamsRules: {
        attr_name: [{ required: true, message: '请输入名称', trigger: 'blur' }],
      },

      // 编辑参数dialog显示与隐藏
      editParamsVisible: false,
      // 编辑参数的dialog表单数据
      editParamsForm: {
        attr_id: '', // 编辑参数的id
        attr_name: '',
      },
      // 编辑参数的dialog表单验证
      editParamsRules: {
        attr_name: [{ required: true, message: '请输入名称', trigger: 'blur' }],
      },
    }
  },
  methods: {
    // 获取所有商品分类数据
    async getGoodsClassifyData() {
      let { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200)
        return this.$message.error('获取商品分类数据失败！')
      this.goodsClassifyData = res.data
    },

    // 级联选择器选项发生改变
    cascaderChange() {
      this.getGoodsParamsData()
    },
    // tabs标签发生改变
    handleTabClick() {
      this.getGoodsParamsData()
    },
    // 封装函数--获取商品参数数据
    async getGoodsParamsData() {
      // 当选项不是最后一级菜单时，清空级联选择器标签内容,清空表格内容，并不发起请求
      if (this.selectKeys.length !== 3) {
        this.selectKeys = []
        this.manyData = []
        this.onlyData = []
        return
      }
      let { data: res } = await this.$http.get(
        `categories/${this.classifyID}/attributes`,
        {
          params: { sel: this.activeName },
        }
      )
      // 表格的下拉列表数据从 res.data.attr_vals中获取，但是是字符串，所以要转为数组
      res.data.forEach((item) => {
        // 因为[""].split(" ") = " " ,为了避免出现空的标签，需要进行判断
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 在添加标签处，需要为v-model和v-if设置自定义的单独属性，否则容易造成牵一发而动全身
        item.inputVisible = false
        item.inputValue = ''
      })
      console.log(res.data)
      if (res.meta.status !== 200)
        return this.$message.error('获取商品参数数据失败！')
      // 根据选中的标签页，将数据添加到对应的属性中
      if (this.activeName === 'many') {
        this.manyData = res.data
      } else {
        this.onlyData = res.data
      }
    },

    // 添加参数dialog关闭
    addParamsClose() {
      this.$refs.addParamsRef.resetFields()
    },
    // 添加参数dialog提交表单数据
    async addParamsSure() {
      let { data: res } = await this.$http.post(
        `categories/${this.classifyID}/attributes`,
        { attr_sel: this.activeName, attr_name: this.addParamsForm.attr_name }
      )
      if (res.meta.status !== 201) return this.$message.error('添加参数失败！')
      this.$message.success('添加参数成功！')
      this.cascaderChange()
      this.addParamsVisible = false
    },

    // 编辑参数dialog表单显示
    showEditParams(row) {
      this.editParamsForm.attr_id = row.attr_id
      this.editParamsForm.attr_name = row.attr_name
      this.editParamsVisible = true
    },
    // 编辑参数dialog关闭
    editParamsClose() {
      this.$refs.editParamsRef.resetFields()
    },
    // 编辑参数dialog提交表单数据
    async editParamsSure() {
      let { data: res } = await this.$http.put(
        `categories/${this.classifyID}/attributes/${this.editParamsForm.attr_id}`,
        {
          attr_name: this.editParamsForm.attr_name,
          attr_sel: this.activeName,
        }
      )
      if (res.meta.status !== 200) return this.$message.error('修改参数失败！')
      this.$message.success('修改参数成功！')
      console.log(this.editParamsForm.attr_id, this.classifyID)
      console.log(res.data)
      this.cascaderChange()
      this.editParamsVisible = false
    },

    // 删除参数
    async deleteParams(attr_id) {
      let result = await this.$confirm(
        '此操作将永久删除该参数, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      if (result === 'cancel') return this.$message.info('你取消了删除操作！')
      let { data: res } = await this.$http.delete(
        `categories/${this.classifyID}/attributes/${attr_id}`
      )
      if (res.meta.status !== 200) return this.$message.error('删除失败！')
      this.$message.success('删除成功！')
      this.cascaderChange()
    },

    // 显示添加标签的input看控件
    showInput(row) {
      row.inputVisible = true
    },
    // 保存标签
    async saveTags(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // 添加新增的标签到标签数组中
      row.attr_vals.push(row.inputValue)
      this.saveTagsRequest(row)
    },
    // 删除标签
    deleteTag(row, i) {
      row.attr_vals.splice(i, 1)
      this.saveTagsRequest(row)
    },
    // 封装函数--发起修改tag标签的请求
    async saveTagsRequest(row) {
      // 发起请求，将修改后的标签数组存储到数据库
      let { data: res } = await this.$http.put(
        `categories/${this.classifyID}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: this.activeName,
          attr_vals: row.attr_vals.join(' '), // attr_vals需要是字符串,要将数组进行转换
        }
      )
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('操作失败！')
      this.$message.success('操作成功！')
      row.inputValue = ''
      row.inputVisible = false
    },
  },

  directives: {
    focus: {
      inserted: function (el) {
        el.querySelector('input').focus()
      },
    },
  },
  computed: {
    // 如果为true，按钮被隐藏，反之亦然
    isBtnDisabled() {
      if (this.selectKeys.length !== 3) {
        return true
      } else {
        return false
      }
    },
    // 获取选中的三级分类id
    classifyID() {
      if (this.selectKeys.length === 3) {
        return this.selectKeys[2]
      }
    },
    // 添加参数dialog的标题
    paramsTitle() {
      if (this.activeName === 'many') {
        return '动态参数'
      } else {
        return '静态属性'
      }
    },
  },
}
</script>

<style lang="less" scoped>
.selectClassify {
  margin-top: 20px;
}
.el-col {
  margin: 0 40px;
}
.el-tag {
  margin: 10px;
}
// 添加标签的input
.input-new-tag {
  width: 100px;
}
</style>